@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;


.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}

.tarjetas {
    display: flex;
    
    .bat_24,
    .bat_48,
    .temp,
    .hume,
    .agua,
    .solar {
        display: block;
        border: 1px solid lighten(c.$blue-grey-200, 10%);
        border-radius: 0.5rem;

        width: 50%;

        margin: 0.5rem;
        padding: 0.5rem;
    }


    .bat_24,
    .bat_48,
    .agua,
    .solar {

        .conte {
           display: flex;
           justify-content: center;
           align-items: center;

           padding: 0.5rem;
           margin-top: 0.5rem;
        }

        img {
            max-width: 3rem;
        }
    }

    .temp,
    .hume {

        .conte {
            display: flex;

            padding: 0.5rem;
            margin-top: 0.5rem;

            .cont {
                display: block;
                text-align: center;

                .parrafo {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }
            }
        }

        img {
            max-width: 4rem;
        }
    }
    .agua, 
    .solar {
        margin-bottom: 1rem;
        
        .parrafo {
            display: flex;
            justify-content: center;
            align-items: center;
        }
    
        img {
            max-width: 4rem;
        }
    }


    .tit {
        text-align: center;
    }

    p {
        padding: 0.5rem;
    }
}

.puerta {
    display: block;
}

.motor{
    padding: 0.5rem;
    margin: 1rem 0;
    border: 1px solid blue;
    
    img {
        max-width: 5rem;
    }

    .contenedor_arriba{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .contenedor_boton {
        width: 100%;
        padding: 1rem;
    }

    .contenedor_contadores{
        text-align: center;

        .contadores {
            display: flex;
            justify-content: space-between;
            align-items: center;

            .totalHoras,
            .totalMes,
            .totalAhora {
                text-align: center;
            }
        }
    }



}

.ilu {
    padding: 0.5rem;
    margin: 1rem 0;
    border: 1px solid blue;
    
    .contenedor_arriba{
        display: flex;
        justify-content: space-between;
        align-items: center;

        img {
            max-width: 5rem;
        }
    }
    .contenedor_botones {
        display: flex;
    }
}